import { FunctionalComponent, h } from '@stencil/core';

export type FcHelpDescriptionType = {
  text: string;
  position: 'bottom' | 'bottom-right';
  hasArrow?: boolean;
  id?: string;
};

const getHelpDescriptionClassNames = (position = 'right', hasArrow = false) => ({
  '_ath-help-description': true,
  [`_ath-help-description--${position}`]: !!position,
  '_ath-help-description--has-arrow': hasArrow,
});

export const FcHelpDescription: FunctionalComponent<FcHelpDescriptionType> = props => {
  return [
    <div class={getHelpDescriptionClassNames(props.position, props.hasArrow)} id={props.id}>
      <div class="content">
        <div class="text">{props.text}</div>
      </div>
    </div>,
  ];
};
